import React from "react";
import axios from "axios";
let baseUrl = "http://localhost:4000/get";

function test(){
  axios.get(baseUrl).then(
    (res) => {
      console.log(res);
  });
  console.log("test");
}
class App extends React.Component{
  constructor(props){
    super(props);
    this.state = {}
  }
  click(){
    console.log("button click");
  }
  render(){
    return(
      <button onClick={() => test()}>click</button>
    )
  }
}

export default App;
